
.header {
  height: 295px;
  background: linear-gradient(92.33deg, #196B78 0.12%, #138496 100%);
  box-shadow: 0 8px 35px rgba(0, 0, 0, 0.16);
  color: #CCF8FF;
  font-family: Inter, sans-serif;
  margin: 0 auto;
  text-align: center;
  position: relative;
  //max-width: 1440px;

}

.title {
  font-style: normal;
  font-weight: bold;
  font-size: 72px;
  line-height: 87px;
  margin: 0;
  padding-top: 64px;
}

.description {
  font-size: 21px;
  line-height: 36px;
  color: rgba(204, 248, 255, 0.75);
  padding-top: 12px;
}

